import { ColumnDef, Row } from '@tanstack/react-table';
import user from '@/assets/imgs/user.png';
import ViewIcon from '@/components/icons/ViewIcon';
import { Attendances } from '../../types/attendances.types';
import { Link } from 'react-router-dom';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Attendances> }) => {
    return (
        <div className="cursor-pointer flex justify-center">
            <Link to={`/human-resources/employee/${row.original?.id}/attendance`}>
                <ViewIcon className="text-primery-blue-400" />
            </Link>
        </div>
    );
};

export const columns: ColumnDef<Attendances>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="flex flex-row justify-start items-center gap-4 text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                <img src={user} alt="" className="rounded-full w-10 h-10 object-cover" />
                <div className="flex flex-col gap-1">
                    <p className="tracking-normal text-primery-blue-900 font-medium text-sm">{row.original?.name}</p>
                    <p className="tracking-normal text-white-blue-800 font-normal text-sm">
                        {`${row.original?.position}`}
                    </p>
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    كود الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {row.original?.id}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الفرع
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.branch?.name}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    {row.original?.status}
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    التقرير
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
